* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  color: #555;
  background: #000;
  width: 100%;
  height: 100%;
  font-family: Helvetica, sans-serif;
  display: block;
  overflow: hidden;
}

:root {
  --vh: 100vh;
}

.wrapper {
  width: 100%;
  height: var(--vh);
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.spline-viewer {
  opacity: 0;
  will-change: opacity;
  transition: opacity 1.5s ease-in;
}

.spline-viewer.loaded {
  opacity: 1;
}

.logo {
  pointer-events: none;
  justify-content: center;
  align-items: center;
  max-width: 770px;
  margin: 0 auto;
  display: flex;
  position: fixed;
  inset: 40px;
}

.footer {
  text-align: center;
  letter-spacing: 15%;
  flex-direction: column;
  gap: 12px;
  font-size: 10px;
  display: flex;
  position: fixed;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
}

.link {
  color: #555;
  transition: all .2s ease-in-out;
}

.link:hover {
  color: #fff;
}
